import React, { Component } from 'react'
import styled from 'styled-components'
import gql from 'graphql-tag'
import { Query } from 'react-apollo'
import DaySelector from './DaySelector'
import Timer from './TimePicker'
import User from './User'

const TEXT_TEMPLATES_QUERY = gql`
  query TEXT_TEMPLATES_QUERY {
    textTemplates {
      id
      name
      content
    }
  }
`

const Back = styled.div`
  display: grid;
  grid-template-columns: 300px 1fr;
  grid-template-rows: 70px 1fr;
  align-items: flex-end;
  width: 100%;
  height: 100%;
  padding: 20px 20px;
  z-index: 5;
  border-radius: 20px;
  border: 5px solid white;
  box-shadow: 1px 0 4px 2px rgba(0, 0, 0, 0.1);
  background-color: rgba(240, 240, 240, 1);
`

// const CharCount = styled.div`
//   display: block;
//   font-size: 11px;
//   bottom: 70px;
//   right: 40px;
//   position: absolute;
//   z-index: 10px;
// `

const MessageEdit = styled.textarea`
  grid-column: 2;
  grid-row: 2;
  position: relative;
  z-index: 6;
  width: 100%;
  padding: 10px;
  height: 100%;
  border-radius: 10px;
  font-size: 2rem;
  /* justify-self: center; */
  resize: none;
  border: none;
  box-shadow: 0 0 4px 3px rgba(0, 0, 0, 0.05);
  &:focus {
    outline: none;
  }
`

const Send = styled.button`
  background: rgba(30, 110, 240, 1);
  color: white;
  font-weight: 800;
  border: none;
  width: 75px;
  font-size: 2rem;

  height: 75px;
  font-size: 1.8rem;
  border-radius: 50%;
  outline: none;
  cursor: pointer;
  box-shadow: 0 0 5px 3px rgba(0, 0, 0, 0.05);
  &:active {
    box-shadow: none;
  }
  &[disabled] {
    opacity: 0.5;
  }
  &:hover {
    background: rgba(30, 110, 240, 0.9);
  }
`

class ReviewMessage extends Component {
  state = {
    time: '8:00',
    date: '',
  }
  render() {
    const time = this.state.time.toString()
    const date = this.state.date.toString()
    return (
      <User>
        {({ data: { me } }) => {
          const business = me.businessName.toString()
          const phone = me.cellPhone.toString()

          return (
            <Back>
              <Query query={TEXT_TEMPLATES_QUERY}>
                {({ data }) => {
                  return (
                    <>
                      <DaySelector />
                      <Timer />
                      <MessageEdit
                        readOnly
                        max={160}
                        value={data.textTemplates[0].content
                          .replace('<business>', business)
                          .replace('<phone>', phone)
                          .replace('<time>', time)
                          .replace('<date>', date)}
                      />
                    </>
                  )
                }}
              </Query>
            </Back>
          )
        }}
      </User>
    )
  }
}
export default ReviewMessage
export { TEXT_TEMPLATES_QUERY }
